import React, {useState, useEffect} from 'react';
import articles from "../../data/article-content";
import {ArticlesComponent} from "../ArticlesComponent";
import {Error404} from "./Error404";
import CommentsList from "./pageComponents/CommentsList";
import UpvoteSection from "./pageComponents/UpvoteSection";
import CommentForm from "./pageComponents/CommentForm";



const HomePage = props =>    {

        const name = props.match.params.name;
        const article = articles.find(article => article.name === name);

        const [articleInfo, setArticleInfo] = useState({upvotes : 0, comments : []})
        useEffect(() =>{
            const fetchData = async () => {
                const result = await fetch(`/api/articles/${name}`);
                const body = await result.json();
                setArticleInfo(body);
            };
            fetchData();
        }, [name])
        if (!article) return <Error404/>;
        const otherArticles = articles.filter(article => article.name !== name);
        return (
            <>

                <h1>{article.title}</h1>
                <UpvoteSection name={name} setArticleInfo={setArticleInfo} upvotes={articleInfo.upvotes} />
                {
                    article.content.map((para, key) => <p key={key}>{para}</p>)
                }
                <CommentForm setArticleInfo={setArticleInfo} name={name}/>
                <CommentsList comments={articleInfo.comments} />

                <h5>You may also like :</h5>
                <ArticlesComponent articles={otherArticles}/>
            </>
        )
}

export default HomePage;